import { By } from 'selenium-webdriver';
import { DriverLike, register } from '@oracle/oraclejet-webdriver';
import { ButtonWebElement } from './ButtonWebElement';
export { ButtonWebElement };

/**
 * Retrieve an instance of [[ButtonWebElement]].
 * @example
 * ```javascript
 * import { findButton } from '@oracle/oraclejet-core-pack/webdriver';
 * const el = await findButton(driver, By.id('my-oj-c-button'));
 * ```
 * @param driver A WebDriver/WebElement instance from where the element will be
 * searched. If WebDriver is passed, the element will be searched globally in the
 * document. If WebElement is passed, the search will be relative to this element.
 * @param by The locator with which to find the element
 */
export async function findButton(driver: DriverLike, by: By): Promise<ButtonWebElement> {
  const webEl = await driver.findElement(by);
  // Check that the element is of type ButtonWebElement
  if (!(webEl instanceof ButtonWebElement)) {
    const tagName = await webEl.getTagName();
    throw Error(
      `findButton(${by}) created ${webEl.constructor.name} for <${tagName}>, but expected ${ButtonWebElement.name}`
    );
  }
  return webEl;
}

register('oj-c-button', ButtonWebElement);
